:root {
  --font-Calibre: Calibre, "San Francisco", "SF Pro Text", -apple-system,
    system-ui, BlinkMacSystemFont, Roboto, "Helvetica Neue", "Segoe UI", Arial,
    sans-serif;
  --font-SFMono: "SF Mono", "Fira Code", "Fira Mono", "Roboto Mono",
    "Lucida Console", Monaco, monospace;

    --primary-bg : #4285F4;
    --secondary-bg:#66757F;
    --success-bg :    #34A853;
    --warning-bg: #FBBC05;
    --danger-bg: #EA4335;
    --light-bg:#F5F5F1;
    --dark-bg:#292F33;
    --info-bg:#55ACEE;
    --white-bg : #ffffff;
    --hnews-bg:#ff6600;

    --primary : #4285F4;
    --secondary:#66757F;
    --success :    #34A853;
    --warning: #FBBC05;
    --danger: #EA4335;
    --light:#F5F5F1;
    --dark:#292F33;
    --info:#55ACEE;
    --white : #ffffff;
    --hnews:#ff6600;

    --fs-12: 12px;
    --fs-13: 13px;
    --fs-14: 14px;
    --fs-15: 15px;
    --fs-16: 16px;
    --fs-18: 18px;
    --fs-20: 20px;
    --fs-36: 36px;


    /* Border Radius */
    --br5 : 5px;
    --br10 : 10px;
    --box-shadow: 0 1px 5px rgba(0, 0, 0, 0.15);

}

