html {
  scroll-behavior: smooth;
  box-sizing: border-box;
  width: 100%;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  width: 100%;
  min-height: 100%;
  overflow-x: hidden;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  line-height: 1.3;
  font-family: var(--font-Calibre);
  background-color: var(--white-bg);
  color: var(--dark)
}

ul {
  margin: 0;
  list-style: none;
  padding-inline-start: 0;
}
